* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "poppins", sans-serif;
}
body,
html {
  overflow-x: hidden;
}

body {
  background-image: url("/src/img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
}
.text-tittle {
  font-size: 2rem;
  background-color: #edf2f7;
  padding: 15px 15px;
  border-radius: 10px;
  font-weight: bold;
  box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
  -webkit-box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
}
.text-conteudo {
  font-size: 1.5rem;
  background-color: #edf2f7;
  padding: 15px 15px;
  border-radius: 10px;
  font-weight: bold;
  box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
  -webkit-box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
}
img {
  display: block;
  transition: all ease 0.3s;
  cursor: pointer;

  &:hover {
    transform: scale(1.1);
    background-size: contain;
  }
}
.container {
  max-width: 1024px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
  gap: 100px;
  height: 100vh;
  width: 100vw;

  &__conteudo {
    max-width: 600px;
    display: grid;
    gap: 10px;
    text-align: center;
  }
  span {
    margin-top: 30px;
    text-align: center;
    font-weight: bold;
    font-size: 3rem;
    color: #000000;
    text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.6);
  }
  &__button {
    background-color: #ba68c8;
    color: #111;
    font-weight: bold;
    padding: 20px 15px;
    border-radius: 5px;
    max-width: fit-content;
    margin-top: 50px;
    transition: all ease 0.3s;
    margin-right: auto;
    margin-left: auto;
    box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
    -webkit-box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);
    -moz-box-shadow: -8px 7px 27px 0px rgba(0, 0, 0, 0.45);

    &:hover {
      background-color: #ead1ee;
      transform: scale(1.1);
    }
  }
}
@media (max-width: 1024px) {
  .container {
    grid-template-columns: 1fr;
    gap: 0px;
    justify-items: center;
    align-content: center;

    img {
      max-width: 300px;
    }
  }
}
@media (max-width: 550px) {
  .container {
    max-width: 300px;

    &__conteudo {
      max-width: 300px;
      gap: 10px;
      h1,
      p {
        text-align: center;
      }
      span {
        margin-top: 10px;
        font-size: 2rem;
      }
      &__button {
        margin-top: 10px;
      }
    }
  }
}
